import { useEffect, useState } from "react";
import { db } from "../../lib/firebase";
import { collection, addDoc, getDocs, query, orderBy } from "firebase/firestore";

export default function ChatPage() {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");

  const fetchMessages = async () => {
    const q = query(collection(db, "messages"), orderBy("timestamp", "asc"));
    const snapshot = await getDocs(q);
    setMessages(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
  };

  const sendMessage = async () => {
    if (!newMessage) return;
    await addDoc(collection(db, "messages"), {
      from: "VocÃª",
      to: "Outro",
      message: newMessage,
      timestamp: new Date()
    });
    setNewMessage("");
    fetchMessages();
  };

  useEffect(() => { fetchMessages(); }, []);

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">ðŸ’¬ Chat</h1>
      <div className="border p-4 h-64 overflow-y-scroll mb-4">
        {messages.map(msg => (
          <p key={msg.id}><strong>{msg.from}:</strong> {msg.message}</p>
        ))}
      </div>
      <input 
        type="text" 
        value={newMessage} 
        onChange={e=>setNewMessage(e.target.value)} 
        placeholder="Digite uma mensagem..." 
        className="border p-2 w-full mb-2"
      />
      <button onClick={sendMessage} className="bg-blue-500 text-white py-2 px-4 rounded">Enviar</button>
    </div>
  );
}
